
@import url("https://fonts.googleapis.com/css?family=VT323");
html {
  font-size: 10px;
}
body {
  font-family: 'VT323', monospace;
  color: #1FF042;
  margin: 0;
  padding: 0;
}
.screen {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #141814;
}
.screen-outer {
  position: absolute;
  top: 5%;
  left: calc(50% - 120rem / 2);
  width: 90%;
  max-width: 120rem;
  height: 90%;
  background-color: #C9D9D3;
  border-radius: 50% / 4rem;
  border: 4px solid #2A2A2A;
}
.screen-inner {
  position: absolute;
  top: 7.5%;
  left: 7.5%;
  width: 85%;
  height: 85%;
  border-radius: 50% / 4rem;
  background-color: #222;
  -webkit-box-shadow: inset 0 0 10em 1em rgba(0, 0, 0, 0.5);
          box-shadow: inset 0 0 10em 1em rgba(0, 0, 0, 0.5);
  border: 2px solid rgba(7, 16, 7, 0.1);
}
.screen-glare {
  position: absolute;
  top: 5%;
  left: 5%;
  width: 90%;
  height: 90%;
  border-radius: 50% / 4rem;
  opacity: 0.4;
  z-index: 1000;
  background: radial-gradient(944.09px at 50% 0%, #102710 0%, rgba(13, 17, 13, 0.23) 100%);
}
.screen-glare::before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50% / 4rem;
    z-index: 1001;
    background-image: -webkit-gradient(linear, left bottom, left top, from(transparent), color-stop(2%, rgba(32, 128, 32, 0.2)), color-stop(3%, rgba(32, 128, 32, 0.8)), color-stop(3%, rgba(32, 128, 32, 0.2)), to(transparent));
    background-image: linear-gradient(0deg, transparent 0%, rgba(32, 128, 32, 0.2) 2%, rgba(32, 128, 32, 0.8) 3%, rgba(32, 128, 32, 0.2) 3%, transparent 100%);
    background-repeat: no-repeat;
    -webkit-animation: flash 7.5s linear 0s infinite;
            animation: flash 7.5s linear 0s infinite;
}
.screen-glare::after {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50% / 4rem;
    z-index: 1001;
    background-image: radial-gradient(ellipse 50% 15% at 50% 15%, rgba(255, 255, 255, 0.05), transparent), radial-gradient(ellipse 50% 10% at 50% 12%, rgba(255, 255, 255, 0.1), transparent), radial-gradient(ellipse 50% 5% at 50% 10%, rgba(255, 255, 255, 0.1), transparent), radial-gradient(ellipse 50% 3% at 50% 9%, rgba(255, 255, 255, 0.1), transparent), radial-gradient(ellipse 200% 20% at 50% 0%, rgba(0, 0, 0, 0.5), transparent), -webkit-gradient(linear, left bottom, left top, color-stop(50%, rgba(0, 0, 0, 0.2)), color-stop(50%, transparent));
    background-image: radial-gradient(ellipse 50% 15% at 50% 15%, rgba(255, 255, 255, 0.05), transparent), radial-gradient(ellipse 50% 10% at 50% 12%, rgba(255, 255, 255, 0.1), transparent), radial-gradient(ellipse 50% 5% at 50% 10%, rgba(255, 255, 255, 0.1), transparent), radial-gradient(ellipse 50% 3% at 50% 9%, rgba(255, 255, 255, 0.1), transparent), radial-gradient(ellipse 200% 20% at 50% 0%, rgba(0, 0, 0, 0.5), transparent), linear-gradient(0deg, rgba(0, 0, 0, 0.2) 50%, transparent 50%);
    background-size: 100%, 100%, 100%, 100%, 100%, 100% 0.25ch;
}
.start-title {
  position: absolute;
  top: 20%;
  left: calc(50% - 90% / 2);
  text-transform: uppercase;
  width: 90%;
  font-size: 7rem;
  text-align: center;
}
.start-notification {
  position: absolute;
  top: 60%;
  left: calc(50% - 40% / 2);
  width: 40%;
  text-transform: uppercase;
  font-size: 30px;
  text-align: center;
  -webkit-animation: blink 2s steps(1) infinite;
          animation: blink 2s steps(1) infinite;
}
.team {
  position: absolute;
  text-transform: uppercase;
  bottom: 4rem;
  right: 10%;
  font-size: 2rem;
}
.test {
  position: relative;
  padding: 10%;
  width: 80%;
  height: 70%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.result {
  position: relative;
  padding: 10%;
  width: 80%;
  height: 70%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.test-question {
  font-size: 5rem;
}
.result-text {
  font-size: 4rem;
}
.result-notification {
  font-size: 3rem;
  -webkit-animation: blink 2s steps(1) infinite;
          animation: blink 2s steps(1) infinite;
}
.answer--active {
  background-color: rgba(31, 240, 66, 0.2);
}
.test-answers {
  font-size: 3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.answer {
  font-size: 3.5rem;
}
.loader {
  position: absolute;
  bottom: 10rem;
  left: 10%;
  width: 80%;
}
.loader-title {
  font-size: 4rem;
  margin: 0 1rem;
}
.loader-bar {
  font-size: 3rem;
}
@-webkit-keyframes blink {
0% {
    opacity: 1;
}
50% {
    opacity: 0;
}
}
@keyframes blink {
0% {
    opacity: 1;
}
50% {
    opacity: 0;
}
}
@-webkit-keyframes flash {
0% {
    background-position: 0 -100vh;
}
50%, 100% {
    background-position: 0 100vh;
}
}
@keyframes flash {
0% {
    background-position: 0 -100vh;
}
50%, 100% {
    background-position: 0 100vh;
}
}



